import React, { Component } from 'react';
import PortfolioItem from './PortfolioItem';
import Data from '../../JsonData/Main/PortfolioData';

class Portfolio extends React.Component {
    render() {
      const itemData = Data.PORTFOLIO.Item;
      const items = itemData.map((item, i) => (
        <PortfolioItem
          key={i}
          title={item.title}
          productImgURL={item.productImgURL}
          playstoreURL={item.playstoreURL}
          appstoreURL={item.appstoreURL}
          playDirectURL={item.playDirectURL}
          smallDescription={item.smallDescription}
        />
      ));

      return (
        <section id="portfolio" className="portfolio py-5 bg-light">
          <div className="container">
            <div className="section-title mb-5 text-center">
              <h2>{Data.PORTFOLIO.TITLE}</h2>
              <p>{Data.PORTFOLIO.TEXT}</p>
            </div>
            <div className="row">
              {items}
            </div>
          </div>
        </section>
      );
    }
}

export default Portfolio;