import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';

class PortfolioItem extends React.Component {
  render() {
    const {
      title,
      productImgURL,
      playstoreURL,
      appstoreURL,
      playDirectURL,
      smallDescription,
    } = this.props;

    // Determine main link (priority: playDirectURL > appstoreURL > playstoreURL)
    const mainLink = playDirectURL !== '#' ? playDirectURL : appstoreURL !== '#' ? appstoreURL : playstoreURL !== '#' ? playstoreURL : null;
    const mainLinkTarget = mainLink ? '_blank' : undefined;
    const mainLinkRel = mainLink ? 'noopener noreferrer' : undefined;

    // Icon links
    const iconLinks = [
      playDirectURL !== '#' && (
        <a
          href={playDirectURL}
          title="Play now"
          target="_blank"
          rel="noopener noreferrer"
          data-bs-toggle="tooltip"
          data-bs-placement="top"
          className="mx-1"
          key="play"
        >
          <i className="bx bx-play-circle fs-4"></i>
        </a>
      ),
      appstoreURL !== '#' && (
        <a
          href={appstoreURL}
          title="App Store"
          target="_blank"
          rel="noopener noreferrer"
          data-bs-toggle="tooltip"
          data-bs-placement="top"
          className="mx-1"
          key="apple"
        >
          <i className="bx bxl-apple fs-4"></i>
        </a>
      ),
      playstoreURL !== '#' && (
        <a
          href={playstoreURL}
          title="Google Play Store"
          target="_blank"
          rel="noopener noreferrer"
          data-bs-toggle="tooltip"
          data-bs-placement="top"
          className="mx-1"
          key="playstore"
        >
          <i className="bx bxl-play-store fs-4"></i>
        </a>
      ),
    ].filter(Boolean);

    // Card content
    const cardContent = (
      <div className="card h-100 shadow-sm portfolio-card border-0">
        <img
          src={productImgURL}
          className="card-img-top img-fluid"
          alt={title}
          style={{ objectFit: 'cover', height: '220px' }}
        />
        <div className="card-body d-flex flex-column">
          <h5 className="card-title fw-bold mb-2">{title}</h5>
          {smallDescription && <p className="card-text text-muted mb-3">{smallDescription}</p>}
          <div className="mt-auto d-flex justify-content-center">{iconLinks}</div>
        </div>
      </div>
    );

    return (
      <div className="col-lg-4 col-md-6 mb-4 d-flex align-items-stretch">
        {mainLink ? (
          <a
            href={mainLink}
            target={mainLinkTarget}
            rel={mainLinkRel}
            className="text-decoration-none w-100"
            tabIndex={0}
            aria-label={`Open ${title}`}
            style={{ cursor: 'pointer' }}
          >
            {cardContent}
          </a>
        ) : (
          cardContent
        )}
      </div>
    );
  }
}

PortfolioItem.defaultProps = {
  playstoreURL: '#',
  appstoreURL: '#',
  playDirectURL: '#',
};

export default PortfolioItem;